import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
    // 
    const VueRouterPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
    }
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import beifen from '@/views/modules/databaseBackup/beifen'
import huanyuan from '@/views/modules/databaseBackup/huanyuan'

     import users from '@/views/modules/users/list'
    import dictionary from '@/views/modules/dictionary/list'
    import gonggao from '@/views/modules/gonggao/list'
    import liuyan from '@/views/modules/liuyan/list'
    import lvseshipin from '@/views/modules/lvseshipin/list'
    import lvseshipinCollection from '@/views/modules/lvseshipinCollection/list'
    import lvseshipinLiuyan from '@/views/modules/lvseshipinLiuyan/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import dictionaryGonggao from '@/views/modules/dictionaryGonggao/list'
    import dictionaryLvseshipin from '@/views/modules/dictionaryLvseshipin/list'
    import dictionaryLvseshipinCollection from '@/views/modules/dictionaryLvseshipinCollection/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'
    import dictionaryShangxia from '@/views/modules/dictionaryShangxia/list'





//2.   
const routes = [{
    path: '/index',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }, {
        path: '/huanyuan',
        name: '',
        component: huanyuan
    }, {
        path: '/beifen',
        name: '',
        component: beifen
    }, {
        path: '/users',
        name: '',
        component: users
    }
    ,{
        path: '/dictionaryGonggao',
        name: '',
        component: dictionaryGonggao
    }
    ,{
        path: '/dictionaryLvseshipin',
        name: '',
        component: dictionaryLvseshipin
    }
    ,{
        path: '/dictionaryLvseshipinCollection',
        name: '',
        component: dictionaryLvseshipinCollection
    }
    ,{
        path: '/dictionarySex',
        name: '',
        component: dictionarySex
    }
    ,{
        path: '/dictionaryShangxia',
        name: '',
        component: dictionaryShangxia
    }
    ,{
        path: '/config',
        name: '',
        component: config
    }


    ,{
        path: '/dictionary',
        name: '',
        component: dictionary
      }
    ,{
        path: '/gonggao',
        name: '',
        component: gonggao
      }
    ,{
        path: '/liuyan',
        name: '',
        component: liuyan
      }
    ,{
        path: '/lvseshipin',
        name: '',
        component: lvseshipin
      }
    ,{
        path: '/lvseshipinCollection',
        name: '',
        component: lvseshipinCollection
      }
    ,{
        path: '/lvseshipinLiuyan',
        name: '',
        component: lvseshipinLiuyan
      }
    ,{
        path: '/yonghu',
        name: '',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '',
    redirect: '/index'
  }, /**/
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})

export default router;
